.CreateTodoButton {
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
    
    transform: rotate(0);
    transition: .3s ease;
}
.CreateTodoButton:hover {
    transform: rotate(224deg);
}
.TodoButton-black{
    background-color: #003367;
    box-shadow: 0px 5px 25px rgba(0, 51, 102, 0.5);
}
.TodoButton-light{
    background-color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}